
.signin{
    color: #000000;
    display:inline-block;
    text-decoration: none;
    font-size: 120%;
  }
  
.header{
    text-align: center;
    font-size: 170%;
    font-weight: 600;
    text-transform: uppercase;
    display:inline-block;
    margin: 40px 8px 10px 8px; 
    color: #000000;
  }
  
  
  
  /* STRUCTURE */
  
  .wrapper {
    display: flex;
    align-items: center;
    flex-direction: column; 
    justify-content: center;
    width: 100%;
    min-height: 100%;
    padding: 20px;
  }
  
  #formContent {
    border-radius: 10px 10px 10px 10px;
    background: none;
    padding: 10px;
    width: 90%;
    max-width: 450px;
    position: relative;
    padding: 0px;
    box-shadow: 0 10px 20px 5px rgba(0,0,0, 0.6);
    text-align: center;
  }
  
  #formFooter {
    padding: 15px;
    text-align: center;
  }
  
  /* FORM TYPOGRAPHY*/
  
  .submit{
    background-color: #000000;
    border: none;
    color: white;
    padding: 15px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 120%;
    box-shadow: 0 10px 30px 0 rgba(95,186,233,0.4);
    border-radius: 5px 5px 5px 5px;
    margin: 20px;
    transition: all 0.3s ease-in-out;
  }
  .submit:hover{
    background-color: rgb(0, 217, 255);
  }
  
  .submit:active{
    transform: scale(0.8);
  }
  .inputplace{
    background: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 120%;
    margin: 5px;
    width: 85%;
    border: 1px solid #f6f6f6;
    transition: all 0.3s;
    border-radius: 5px 5px 5px 5px;
  }
  
  .inputplace:focus {
    background-color: black;
  }
  
  ::placeholder{
      color: rgba(255, 255, 255, 0.5);
  }
  
  /* Simple CSS3 Fade-in Animation */
  .underlineHover:after {
    display: block;
    left: 0;
    bottom: -10px;
    width: 0;
    height: 2px;
    background-color: #56baed;
    content: "";
    transition: width 0.2s;
  }
  
  .underlineHover:hover {
    color: #0d0d0d;
  }
  
  .underlineHover:hover:after{
    width: 100%;
  }
  
  *:focus {
      outline: none;
  } 
  
  * {
    box-sizing: border-box;
  }